.automatic-authorizer__content--hidden {
    display: none;
}

.automatic-authorizer__logging-screen {
    width: 100vw;
    height: calc(100vh - 72px);
    display: none;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.automatic-authorizer__logging-screen--visible {
    display: -ms-flexbox;
    display: flex;
}

.automatic-authorizer__logging-screen-preloader {
    width: 158px;
    height: 24px;
    background-image: url(data:image/svg+xml;base64,);
}


